.dpzvc-header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}
.dpzvc-header + div {
  padding-top: 50px;
}
.dpzvc-header > header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  background: #008cee;
  color: #fff;
}
.dpzvc-header .left,
.dpzvc-header .right {
  height: 100%;
  font-size: 12px;
  display: flex;
  align-items: center;
}
.dpzvc-header .left {
  padding-left: 12px;
}
.dpzvc-header .left .arrow {
  width: 8px;
  height: 8px;
  border-top: 1px solid #ffffff;
  border-left: 1px solid #ffffff;
  transform: rotate(-45deg);
  display: inline-block;
}
.dpzvc-header .left .text {
  display: inline-block;
}
.dpzvc-header .right {
  padding-right: 12px;
}
.dpzvc-header .title {
  flex: 1;
  font-size: 18px;
  padding: 0 12px;
  text-align: center;
}
.dpzvc-header.dpzvc-header-fixed > header {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 3;
}
.dpzvc-header .header-place {
  padding-top: 50px;
}
.dpzvc-actionSheet {
  background: #eeeeee;
  font-size: 15px;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 300;
  width: 100%;
}
.dpzvc-actionSheet-wrapper {
  width: 100%;
  height: auto;
  margin-bottom: 5px;
