.dpzvc-actionSheet-wrapper-action {
  width: 100%;
  height: 45px;
  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #eeeeee;
}
.dpzvc-actionSheet-wrapper-action:nth-last-child(1) {
  border-bottom: none;
}
.dpzvc-actionSheet-cancle {
