dth: 100%;\n        transition: all .2s ease-in;\n\n        li {\n          width: 100%;\n          height: 35px;\n          line-height: 35px;\n          overflow: hidden;\n          white-space: nowrap;\n          text-overflow: ellipsis;\n          color: @PickerTxtColor;\n\n          &.level_1 {\n            transform: rotateY(15deg);\n            transform-style: preserve-3d;\n            font-size: @PickerTxtSize - 2;\n            opacity: .7;\n          }\n\n          &.level_1_1 {\n            transform: rotateY(-15deg);\n            transform-style: preserve-3d;\n            font-size: @PickerTxtSize - 2;\n            opacity: .7;\n          }\n\n          &.level_2 {\n            transform: rotateY(20deg);\n            transform-style: preserve-3d;\n            font-size: @PickerTxtSize - 3;\n            opacity: .5;\n          }\n\n          &.level_2_1 {\n            transform: rotateY(-20deg);\n            transform-style: preserve-3d;\n            font-size: @PickerTxtSize - 3;\n            opacity: .5;\n          }\n\n          &.level_3 {\n            transform: rotateY(45deg);\n            transform-style: preserve-3d;\n            font-size: @PickerTxtSize - 4;\n            opacity: .3;\n          }\n\n          &.level_3_1 {\n            transform: rotateY(-45deg);\n            transform-style: preserve-3d;\n            font-size: @PickerTxtSize - 4;\n            opacity: .3;\n          }\n\n          &.current {\n            font-size: @PickerTxtSize;\n            font-weight: bold;\n          }\n        }\n      }\n\n\n    }\n\n  }\n\n\n\n  .dpzvc-picker-slot ul.dpzvc-picker-slot-dragging {\n    transition: none;\n  }\n\n}",".dpzvc-tab {\n  background: @TabBgColor;\n  position: absolute;\n  height: @TabHeight;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  box-sizing: content-box;\n\n\n  ul {\n    display: flex;\n    height: 100%;\n\n    li {\n      position: relative;\n      flex: 1;\n      width: 0%;\n      height: 100%;\n      color: @TabTxtColor;\n      font-size: @TabTxtSize;\n      line-height: @TabTxtSize;\n\n      a {\n        width: 100%;\n        display: inline-block;\n        position: absolute;\n        top: 50%;\n        left: 50%;\n        transform: translate3d(-50%, -50%, 0);\n        vertical-align: middle;\n        text-align: center;\n        color: #666666;\n\n        i {\n          display: inline-block;\n          font-size: 22px;\n          line-height: 1;\n          margin-top: 2px;\n          text-align: center;\n        }\n\n\n        span {\n          display: inline-block;\n          width: 100%;\n          text-align: center;\n          line-height: 23px;\n        }\n\n        p {\n          width: 100%;\n          display: inline-block;\n        }\n\n\n        &.cur {\n          color: @TabTxtColorCur;\n        }\n\n      }\n\n\n\n    }\n  }\n\n\n}",".dpzvc-modal {\n  transform: translate3d(-50%, -50%, 0) scale(1);\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  overflow: hidden;\n  border-radius: @BorderRadius;\n  background: @ModalBgColor;\n  z-index: 100;\n  color: @ModalColor;\n\n  .dpzvc-modal-header {\n    text-align: center;\n    padding: 0 10px;\n\n    //border-bottom: 1px solid @BorderColor;\n    .dpzvc-modal-header-inner {\n      font-weight: bold;\n      font-size: 20px;\n      width: 100%;\n      padding-top: 20px;\n    }\n  }\n\n  .dpzvc-modal-body {\n\n    .dpzvc-modal-body-inner {\n      text-align: center;\n    }\n\n    line-height:25px;\n    padding:30px 20px;\n    word-wrap:break-word;\n    overflow-y:scroll;\n  }\n\n  .dpzvc-modal-footer {\n    width: 100%;\n    display: flex;\n    justify-content: center;\n    flex-direction: row;\n    overflow: hidden;\n\n    .dpzvc-button {\n      border-top: 1px solid @BorderColor;\n    }\n\n    //.dpzvc-button:nth-of-type(1){\n    //  border-right:1px solid @BorderColor;\n    //}\n  }\n\n  .dpzvc-modal-button {\n    flex: 1;\n    height: 40px;\n    border: 1px solid @BorderColor;\n    border-left: 0 none;\n  }\n\n  .dpzvc-modal-button-sure {\n\n    color: red;\n  }\n\n  //.v-lc-modal-button-cancle,.dpzvc-prompt-button-cancle{\n  //\n  //  color: #ccc;\n  //}\n}\n\n//\n.dpzvc-modal-mask {\n  position: fixed;\n  left: 0;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  width: 100%;\n  height: 100%;\n  background: rgba(0, 0, 0, .4);\n  z-index: 99;\n}",".dpzvc-prompt-content {\n\n  width: 100%;\n\n  .dpzvc-prompt-spec {\n    display: inline-block;\n    width: 100%;\n    line-height: 25px;\n    text-indent: 5px;\n    color: #555;\n    font-size: 13px;\n  }\n\n\n\n}\n\n.dpzvc-prompt-button {\n  width: 50%;\n  border: 1px solid #eee;\n  border-left: 0 none;\n\n\n  button {\n    background: #ffffff;\n    color: red;\n  }\n}\n\n.dpzvc-prompt-error {\n\n  color: red;\n  text-align: left;\n  text-indent: 5px;\n  line-height: 25px;\n  height: 25px;\n}",".dpzvc-slideBar {\n  border-top-color: @SlideBarBgColor;\n  border-bottom-color: @SlideBarBgColor;\n  border-top-style: solid;\n  border-bottom-style: solid;\n  border-bottom-width: 1px;\n  border-top-width: 1px;\n\n  //border-bottom: 1px solid #eee;\n  //border-top: 1px solid #eee;\n  .dpzvc-slideBar-child {\n    overflow: hidden;\n\n    background-color: @SlideBackgroundCorlor;\n\n    >a {\n\n      color: @slideChildColor;\n    }\n  }\n}\n\n.dpzvc-slideBar {\n  display: block;\n  width: 100%;\n  background: #ffffff;\n  overflow: hidden;\n  position: relative;\n\n  &-header {\n    overflow-x: scroll;\n    overflow-y: hidden;\n    width: 100%;\n    position: relative;\n    //&.fixed {\n    //  position: absolute;\n    //  top:0;\n    //  left:0;\n    //  z-index:10;\n    //  background: #fff;\n    //}\n  }\n}\n\n.dpzvc-slideBar-wrapper {\n\n\n  //border-bottom: 1px solid #eeeeee;\n}\n\n.dpzvc-slideBar-wrapper-absolute {\n  position: absolute;\n  bottom: 0;\n  height: 2px;\n  background-color: @ThemeColor;\n  transition: transform .2s ease-out;\n  will-change: transform;\n  z-index: 3;\n}\n\n//.dpzvc-slideBar-wrapper::before {\n//  width:100%;\n//  bottom:0;\n//  left: 0;\n//  position: absolute;\n//  content: '';\n//  height: 1px;\n//  background: #eeeeee;\n//\n//}\n\n.dpzvc-slideBar-wrapper .dpzvc-slideBar-child {\n  height: 100%;\n  line-height: 1;\n}\n\n.dpzvc-slideBar-wrapper .dpzvc-slideBar-child.active>a {\n  color: @ThemeColor;\n}\n\n.dpzvc-slideBar-wrapper .dpzvc-slideBar-child a {\n  display: inline-block;\n  width: 100%;\n}\n\n.dpzvc-slideBar-wrapper.dpzvc-slideBar-flex {\n  display: flex;\n  align-items: center;\n\n}\n\n.dpzvc-slideBar-wrapper.dpzvc-slideBar-flex.normal {\n  flex-direction: row;\n}\n\n.dpzvc-slideBar-wrapper.dpzvc-slideBar-flex.vertical {\n  flex-direction: column;\n}\n\n.dpzvc-slideBar-wrapper.dpzvc-slideBar-flex .dpzvc-slideBar-child {\n  flex: 1;\n  width: 0;\n}\n\n.dpzvc-slideBar-slide {\n  width: auto;\n}\n\n.dpzvc-slideBar-slide .dpzvc-slideBar-child {\n  display: inline-block;\n  margin-bottom: -3px;\n}\n\n//.dpzvc-slideBar-slide .dpzvc-slideBar-child.normalChild {\n//  height: 30px;\n//}\n\n.dpzvc-slideBar-container {\n  width: 100%;\n  position: relative;\n  left: 0;\n  top: 0;\n  overflow: hidden;\n}\n\n.dpzvc-slideBar-content {\n  transition: transform .2s ease-in;\n  will-change: transform;\n  height: 100%;\n  //display: flex;\n  //flex-flow: row nowrap;\n\n  overflow: auto;\n}\n\n.dpzvc-slideBar-content.dpzvc-slideBar-dragging {\n  transition: none;\n  will-change: none;\n}\n\n.dpzvc-slideBar-content-item {\n  //flex: 1;\n  //width:0;\n  display: inline-block;\n  height: 100%;\n\n}","@width: 40px;\n\n.dpzvc-button {\n  position: relative;\n  width: 100%;\n  overflow-x: hidden;\n  box-sizing: content-box;\n\n\n  .loading {\n    display: inline-block;\n    vertical-align: middle;\n  }\n\n  &-radius {\n    border-radius: 5px;\n  }\n\n}\n\n.dpzvc-button.dpzvc-button-circle {\n\n  width: @width;\n  height: @width;\n  border-radius: 50%\n}\n\n.dpzvc-button .dpzvc-button-btn {\n  width: 100%;\n  height: 100%;\n\n  &::after {\n    position: absolute;\n    content: '';\n    left: 0;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    background: #000;\n    opacity: 0;\n  }\n\n}\n\n.dpzvc-button-inline {\n  letter-spacing: -3px;\n}\n\n.dpzvc-button-inline>button {\n\n  letter-spacing: 0;\n}\n\n.dpzvc-button-success {\n  background: @ButtonSuccessBgColor;\n  color: @ButtonColor;\n  border-color: @ButtonSuccessBgColor;\n\n  &:active {\n    background: @SuccessActiveBgColor;\n    border-color: @SuccessActiveBgColor;\n  }\n}\n\n.dpzvc-button-danger {\n  color: @ButtonColor;\n  background-color: @ButtonDangerBgColor;\n  border-color: @ButtonDangerBgColor;\n\n  &:active {\n    background: @DangerActiveBgColor;\n    border-color: @DangerActiveBgColor;\n  }\n}\n\n.dpzvc-button-primary {\n  color: #4d4d4d;\n  background-color: @ButtonPrimaryBgColor;\n  border-color: @ButtonPrimaryBgColor;\n\n  &:active {\n    background-color: @PrimaryActiveBgColor;\n    border-color: @PrimaryActiveBgColor;\n  }\n}\n\n.dpzvc-button-warning {\n  color: @ButtonColor;\n  background-color: @ButtonWarningBgColor;\n  border-color: @ButtonWarningBgColor;\n\n  &:active {\n    background-color: @WarningActiveBgColor;\n    border-color: @WarningActiveBgColor;\n  }\n}\n\n.dpzvc-button-normal {\n  color: @ButtonColor;\n  background-color: @ButtonNormalBgColor;\n  border-color: @ButtonNormalBgColor;\n\n  &:active {\n    background-color: @NormalActiveBgColor;\n    border-color: @NormalActiveBgColor;\n  }\n}\n\n.dpzvc-button-text {\n  color: #657180;\n  background-color: @ButtonTextBgColor;\n  border-color: @ButtonTextBgColor;\n\n  &:active {\n    background-color: @TextActiveBgColor;\n    border-color: @TextActiveBgColor;\n  }\n}\n\n.dpzvc-button-btn.dpzvc-button-disabled {\n  color: @ButtonColor;\n  opacity: .6;\n}",".dpzvc-checkbox {\n  display: inline-block;\n  margin-right: 8px;\n}\n\n.dpzvc-checkBoxGroup-vertical .dpzvc-checkbox {\n  margin-right: 0;\n  margin-bottom: 8px;\n}\n\n.dpzvc-checkbox-wrapper {\n  display: inline-block;\n  vertical-align: middle;\n  white-space: nowrap;\n  cursor: pointer;\n  outline: 0;\n  line-height: 1;\n  position: relative\n}\n\n.dpzvc-checkbox-inner {\n  display: inline-block;\n  width: 14px;\n  height: 14px;\n  position: relative;\n  top: 0;\n  left: 0;\n  border: 1px solid #d7dde4;\n  border-radius: 2px;\n  background-color: #fff;\n  transition: border-color .2s ease-in-out, background-color .2s ease-in-out;\n\n  &:after {\n    content: \"\";\n    display: table;\n    width: 4px;\n    height: 8px;\n    position: absolute;\n    top: 1px;\n    left: 4px;\n    border: 2px solid #fff;\n    border-top: 0;\n    border-left: 0;\n    transform: rotate(45deg) scale(0);\n    transition: all .2s ease-in-out;\n  }\n}\n\n.dpzvc-checkbox-checked {\n  .dpzvc-checkbox-inner {\n    border-color: @checkBoxColor;\n    background-color: @checkBoxColor;\n\n    &:after {\n      content: \"\";\n      display: table;\n      width: 4px;\n      height: 8px;\n      position: absolute;\n      top: 1px;\n      left: 4px;\n      border: 2px solid #fff;\n      border-top: 0;\n      border-left: 0;\n      transform: rotate(45deg) scale(1);\n      transition: all .2s ease-in-out;\n    }\n  }\n\n  .dpzvc-checkbox-disable+span {\n    color: #f3f3f3;\n    cursor: not-allowed;\n  }\n}\n\n.dpzvc-checkbox-input {\n  width: 100%;\n  height: 100%;\n  position: absolute;\n  left: 0;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  opacity: 0;\n  z-index: 1;\n  cursor: pointer;\n}\n\n.dpzvc-checkbox-disable {\n  .dpzvc-checkbox-inner {\n    background-color: #f3f3f3;\n    border-color: #d7dde4;\n\n    input {\n      cursor: not-allowed;\n    }\n\n  }\n\n  &.dpzvc-checkbox-checked {\n    .dpzvc-checkbox-inner {\n      background-color: #f3f3f3;\n      border-color: #d7dde4;\n    }\n  }\n}\n\n.dpzvc-checkbox-disable+span {\n  color: #f3f3f3;\n  cursor: not-allowed;\n}","//.dpzvc-radiobox-checked{\n//  .dpzvc-radiobox-inner{\n//\n//    //border-color: @checkBoxColor!important;\n//    border-color: #555!important;\n//    &:after{\n//      background-color:@radioBoxColor!important;\n//    }\n//  }\n//}\n.dpzvc-radioBoxGroup.dpzvc-radioBoxGroup-vertical .dpzvc-radiobox {\n  margin-right: 0;\n  margin-bottom: 8px;\n}\n\n.dpzvc-radiobox {\n  display: inline-block;\n  margin-right: 8px;\n}\n\n\n.dpzvc-radiobox-wrapper {\n  display: inline-block;\n  vertical-align: middle;\n  white-space: nowrap;\n  cursor: pointer;\n  outline: 0;\n  line-height: 1;\n  position: relative\n}\n\n.dpzvc-radiobox-inner {\n  display: inline-block;\n  width: 14px;\n  height: 14px;\n  position: relative;\n  top: 0;\n  left: 0;\n  border: 1px solid #d7dde4;\n  border-radius: 100%;\n  background-color: #fff;\n  transition: border-color .2s ease-in-out, background-color .2s ease-in-out;\n}\n\n\n\n.dpzvc-radiobox-inner:before {\n  position: absolute;\n  width: 14px;\n  height: 14px;\n  left: 0px;\n  top: 0px;\n  border-radius: 50%;\n  display: table;\n  border-top: 0;\n  border-left: 0;\n  content: \" \";\n  background-color: @radioBoxColor;\n  opacity: 0;\n  transition: all .2s ease-in-out;\n  transform: scale(0);\n}\n\n.dpzvc-radiobox-inner:after {\n  position: absolute;\n  width: 6px;\n  height: 6px;\n  left: 4px;\n  top: 4px;\n  border-radius: 50%;\n  display: table;\n  border-top: 0;\n  border-left: 0;\n  content: \" \";\n  background-color: #ffffff;\n  opacity: 0;\n  transition: all .2s ease-in-out;\n  transform: scale(0);\n}\n\n.dpzvc-radiobox-checked .dpzvc-radiobox-inner:after,\n.dpzvc-radiobox-checked .dpzvc-radiobox-inner:before {\n  opacity: 1;\n  transform: scale(1);\n  transition: all .2s ease-in-out;\n  box-shadow: 0 0 1px 1px @ThemeColor\n}\n\n\n.dpzvc-radiobox-disable.dpzvc-radiobox-checked .dpzvc-radiobox-inner:after,\n.dpzvc-radiobox-disable.dpzvc-radiobox-checked .dpzvc-radiobox-inner:before {\n  background-color: #f3f3f3;\n  cursor: not-allowed;\n}\n\n.dpzvc-radiobox-disable .dpzvc-radiobox-inner:after,\n.dpzvc-radiobox-disable .dpzvc-radiobox-inner:before {\n  background-color: #f3f3f3;\n  cursor: not-allowed;\n}\n\n\n.dpzvc-radiobox-checked.dpzvc-radiobox-disable .dpzvc-radiobox-inner {\n  border-color: #f3f3f3;\n  cursor: not-allowed;\n}\n\n.dpzvc-radiobox-disable input {\n  cursor: not-allowed;\n}\n\n.dpzvc-radiobox-disable .dpzvc-radiobox-inner {\n  border-color: #f3f3f3;\n  cursor: not-allowed;\n}\n\n.dpzvc-radiobox-disable+span {\n  color: #f3f3f3;\n  cursor: not-allowed;\n}\n\n\n\n\n.dpzvc-radiobox-input {\n  width: 100%;\n  height: 100%;\n  position: absolute;\n  left: 0;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  opacity: 0;\n  z-index: 1;\n  cursor: pointer;\n}",".ace-twilight{\n  background-color: #555!important;\n}",".dpzvc-number {\n\n  width: 100%;\n  display: flex;\n  flex-direction: row;\n  justify-content: flex-start;\n  align-items: center;\n  color: #4a4a4a;\n\n\n  & input {\n    box-sizing: border-box;\n    border: .5px solid #c0c4cc;\n    text-align: center;\n    transition: border-color .2s linear;\n    will-change: border-color;\n    background: #ffffff;\n  }\n\n  & input:focus {\n    border-color: @ThemeColor\n  }\n\n  & .disabled {\n\n    color: #c0c4cc;\n\n\n  }\n\n  &-changevalue {\n    align-items: center;\n    position: relative;\n    box-sizing: border-box;\n    display: inline-block;\n    text-align: center;\n    border: .5px solid #c0c4cc;\n    background: #e1e2ea;\n\n\n\n\n  }\n\n  &-changevalue.left {\n    border-bottom-left-radius: 3px;\n    border-top-left-radius: 3px;\n    border-right: 0;\n\n  }\n\n  &-changevalue.right {\n    border-bottom-right-radius: 3px;\n    border-top-right-radius: 3px;\n    border-left: 0;\n\n  }\n}",".dpzvc-textBar {\n\n  background: #ffffff;\n  overflow: hidden;\n\n  &-input {\n    width: 100%;\n    display: inline-block;\n\n    &>input {\n      border: 1px solid @TextBorderColor;\n      border-radius: 0;\n      height: @TextInputHeight;\n      box-sizing: border-box;\n      width: 100%;\n      padding-left: 10px;\n      color: @TextColor;\n      transition: all .2s linear;\n    }\n\n    &>input:focus {\n\n      border-color: @ThemeColor;\n      box-shadow: 0 0 2px @ThemeColor\n    }\n\n    &>input::placeholder {\n      color: @TextPlaceholderColor;\n      font-family: \"Microsoft Tai Le\";\n    }\n  }\n\n\n  &-textarea {\n\n    width: 100%;\n    display: inline-block;\n\n    &>textarea {\n      width: 100%;\n      padding: 10px 5px;\n      box-sizing: border-box;\n      height: @TextAreaHeight;\n      border: 1px solid @TextBorderColor;\n      border-radius: 0;\n      color: @TextColor;\n      transition: border-color .2s linear;\n      will-change: border-color;\n      font-size: 12px;\n    }\n\n    &>textarea:focus {\n      border-color: #39f;\n      box-shadow: 0 0 2px #39f\n    }\n\n    &>textarea::placeholder {\n      color: #e5e5e5;\n      font-family: \"Microsoft Tai Le\";\n    }\n  }\n\n\n\n  &-tip {\n    flex: 1;\n    align-self: center;\n    text-indent: 3px;\n    line-height: 1;\n    font-size: 12px;\n  }\n\n  &-tip.success {\n    color: lightgreen;\n  }\n\n  &-tip.error {\n    color: red;\n  }\n}",".dpzvc-swipe {\n  width: 100%;\n  position: relative;\n  height: auto;\n  overflow-x: hidden;\n\n  &-wrapper {\n\n    position: relative;\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    transition: transform .2s ease-out;\n    will-change: transform;\n    display: flex;\n    flex-direction: row;\n    justify-content: flex-start;\n    align-items: center;\n  }\n\n  &-item {\n    flex: 1;\n\n    &.multiple {\n      display: flex;\n      flex-direction: row;\n      justify-content: flex-start;\n\n    }\n\n    >img {\n      display: block;\n      width: 100%;\n      height: 80%;\n      object-fit: cover;\n    }\n\n    >span {\n      display: block;\n      width: 100%;\n      line-height: 25px;\n      text-align: center;\n\n    }\n  }\n\n  &-single {\n    display: block;\n\n    >img {\n      display: block;\n      width: 100%;\n      height: 80%;\n      object-fit: cover;\n    }\n\n    >span {\n      display: block;\n      width: 100%;\n      line-height: 25px;\n      text-align: center;\n    }\n\n  }\n\n  &-multiple {\n    display: block;\n    flex: 1;\n    height: 100%;\n\n    >img {\n      display: block;\n      width: 100%;\n      height: 80%;\n      object-fit: cover;\n    }\n\n    >span {\n      display: block;\n      width: 100%;\n      line-height: 25px;\n      text-align: center;\n    }\n  }\n\n  &-dragging {\n    transition: none;\n    will-change: none\n  }\n\n  &-dots {\n    position: absolute;\n    left: 0;\n    width: 100%;\n    display: flex;\n    justify-content: center;\n    flex-direction: row;\n    align-items: center;\n\n    &-item {\n      margin: 0 2px;\n      background: #ffffff;\n      opacity: 0.3;\n      width: 7px;\n      height: 7px;\n      border-radius: 50%;\n      display: inline-block;\n\n    }\n\n    &-bottom {\n      bottom: 20px;\n    }\n\n    &-top {\n      top: 20px;\n    }\n  }\n\n  .dpzvc-swipe-dots-item.active {\n    opacity: 1;\n  }\n\n}",".dpzvc-switch-ui {\n  display: block;\n  width: 35px;\n  height: 20px;\n  background: #ccc;\n  border-radius: 100px;\n  cursor: pointer;\n  position: relative;\n  -webkit-transition: all .3s ease;\n  -moz-transition: all .3s ease;\n  transition: all .3s ease;\n\n  &.small {\n    width: 24px;\n    height: 14px;\n  }\n\n  &:after {\n    content: '';\n    display: block;\n    width: 18px;\n    height: 18px;\n    border-radius: 100px;\n    background: #fff;\n    box-shadow: 0 1px 1px rgba(0, 0, 0, .1);\n    position: absolute;\n    left: 1px;\n    top: 1px;\n    -webkit-transition: all .3s ease;\n    -moz-transition: all .3s ease;\n    transition: all .3s ease;\n  }\n\n\n  &.small:after {\n    width: 12px;\n    height: 12px;\n  }\n\n  &:active:after {\n    width: 24px;\n  }\n\n  &.small:active:after {\n    width: 16px;\n  }\n\n\n\n\n}\n\n\n\n\n.dpzvc-switch-checkbox:checked~label {\n  background: #39f;\n}\n\n.dpzvc-switch-checkbox:checked~label:after {\n  left: 16px;\n}\n\n.dpzvc-switch-checkbox:checked~label:active:after {\n  left: 10px;\n}\n\n\n.dpzvc-switch-checkbox:checked~label.small:after {\n  left: 11px;\n}\n\n.dpzvc-switch-checkbox:checked~label.small:active:after {\n  left: 7px;\n}\n\n.dpzvc-switch-checkbox:disabled~label {\n  background: #d5d5d5;\n  pointer-events: none;\n}\n\n.dpzvc-switch-checkbox:disabled~label:after {\n  background: #bcbdbc;\n}\n\n.dpzvc-switch-checkbox.hidden {\n  display: none;\n}",".dpzvc-upload {\n  position: relative;\n\n  &-wrapper {\n    width: 100%;\n    height: 100%;\n    background: #39f;\n    color: #ffffff;\n    line-height: 50px;\n    text-align: center;\n    border-radius: 10px;\n  }\n\n  input[type=file] {\n    position: absolute;\n    left: 0;\n    top: 0;\n    opacity: 0;\n    width: 100%;\n    height: 100%;\n  }\n\n}","// spinner.less -  unit()/round()\n@SpinnerSize: 28px;\n@SpinnerColor: #ccc;\n\n// ---------- Blade Spinner ----------\n.dpzvc-spinner-blade {\n  position: relative;\n  height: @SpinnerSize;\n  width: @SpinnerSize;\n\n  .blade {\n    position: absolute;\n    left: 44.5%;\n    top: 37%;\n    width: 10%;\n    height: 25%;\n    border-radius: ~'50% / 20%';\n    animation: dpzvc-spinner-blade 1s linear infinite;\n    background-color: @SpinnerColor;\n  }\n\n  .blade-0 {\n    transform: rotate(30deg) translate(0, -150%);\n    animation-delay: -1.6667s;\n  }\n\n  .blade-1 {\n    transform: rotate(60deg) translate(0, -150%);\n    animation-delay: -1.5s;\n  }\n\n  .blade-2 {\n    transform: rotate(90deg) translate(0, -150%);\n    animation-delay: -1.3333s;\n  }\n\n  .blade-3 {\n    transform: rotate(120deg) translate(0, -150%);\n    animation-delay: -1.1667s;\n  }\n\n  .blade-4 {\n    transform: rotate(150deg) translate(0, -150%);\n    animation-delay: -1s;\n  }\n\n  .blade-5 {\n    transform: rotate(180deg) translate(0, -150%);\n    animation-delay: -0.8333s;\n  }\n\n  .blade-6 {\n    transform: rotate(210deg) translate(0, -150%);\n    animation-delay: -0.6667s;\n  }\n\n  .blade-7 {\n    transform: rotate(240deg) translate(0, -150%);\n    animation-delay: -0.5s;\n  }\n\n  .blade-8 {\n    transform: rotate(270deg) translate(0, -150%);\n    animation-delay: -0.3333s;\n  }\n\n  .blade-9 {\n    transform: rotate(300deg) translate(0, -150%);\n    animation-delay: -0.1667s;\n  }\n\n  .blade-10 {\n    transform: rotate(330deg) translate(0, -150%);\n    animation-delay: 0s;\n  }\n\n  .blade-11 {\n    transform: rotate(360deg) translate(0, -150%);\n    animation-delay: 0.1667s;\n  }\n}\n\n@keyframes dpzvc-spinner-blade {\n  0% {\n    opacity: 0.85;\n  }\n\n  50% {\n    opacity: 0.25;\n  }\n\n  100% {\n    opacity: 0.25;\n  }\n}\n\n// ---------- Double Bounce Spinner ----------\n.dpzvc-spinner-double-bounce {\n  position: relative;\n  height: @SpinnerSize;\n  width: @SpinnerSize;\n\n  .bounce {\n    width: 100%;\n    height: 100%;\n    border-radius: 50%;\n    opacity: 0.6;\n    position: absolute;\n    top: 0;\n    left: 0;\n    animation: dpzvc-spinner-double-bounce 2s infinite ease-in-out;\n    background-color: @SpinnerColor;\n  }\n\n  .bounce-1 {\n    animation-delay: -1s;\n  }\n}\n\n@keyframes dpzvc-spinner-double-bounce {\n\n  0%,\n  100% {\n    transform: scale(0);\n  }\n\n  50% {\n    transform: scale(1);\n  }\n}\n\n// ---------- Triple Bounce Spinner ----------\n@unitSize: @SpinnerSize / 3; // 28px / 3 = 9.3333px\n\n.dpzvc-spinner-triple-bounce {\n  font-size: 0;\n  padding: (@SpinnerSize - @unitSize) / 2 0;\n\n  .bounce {\n    border-radius: 100%;\n    display: inline-block;\n    animation: dpzvc-spinner-triple-bounce 1.4s infinite ease-in-out both;\n    background-color: @SpinnerColor;\n    width: @unitSize;\n    height: @unitSize;\n  }\n\n  .bounce-0 {\n    animation-delay: -0.32s;\n  }\n\n  .bounce-1 {\n    animation-delay: -0.16s;\n    margin: 0 (@unitSize * 0.4);\n  }\n}\n\n@keyframes dpzvc-spinner-triple-bounce {\n\n  0%,\n  80%,\n  100% {\n    transform: scale(0);\n  }\n\n  40% {\n    transform: scale(1);\n  }\n}\n\n// ---------- Fading Circle Spinner ----------\n.dpzvc-spinner-fading-circle {\n  position: relative;\n  height: @SpinnerSize;\n  width: @SpinnerSize;\n\n  .block {\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    left: 0;\n    top: 0;\n\n    .circle {\n      margin: 0 auto;\n      width: 15%;\n      height: 15%;\n      border-radius: 100%;\n      animation: dpzvc-spinner-fading-circle 1.2s infinite ease-in-out both;\n      background-color: @SpinnerColor;\n    }\n  }\n\n  //  12  block \n  .block-0 {\n    transform: rotate(0deg);\n\n    .circle-0 {\n      animation-delay: -1.2s;\n    }\n  }\n\n  .block-1 {\n    transform: rotate(30deg);\n\n    .circle-1 {\n      animation-delay: -1.1s;\n    }\n  }\n\n  .block-2 {\n    transform: rotate(60deg);\n\n    .circle-2 {\n      animation-delay: -1s;\n    }\n  }\n\n  .block-3 {\n    transform: rotate(90deg);\n\n    .circle-3 {\n      animation-delay: -0.9s;\n    }\n  }\n\n  .block-4 {\n    transform: rotate(120deg);\n\n    .circle-4 {\n      animation-delay: -0.8s;\n    }\n  }\n\n  .block-5 {\n    transform: rotate(150deg);\n\n    .circle-5 {\n      animation-delay: -0.7s;\n    }\n  }\n\n  .block-6 {\n    transform: rotate(180deg);\n\n    .circle-6 {\n      animation-delay: -0.6s;\n    }\n  }\n\n  .block-7 {\n    transform: rotate(210deg);\n\n    .circle-7 {\n      animation-delay: -0.5s;\n    }\n  }\n\n  .block-8 {\n    transform: rotate(240deg);\n\n    .circle-8 {\n      animation-delay: -0.4s;\n    }\n  }\n\n  .block-9 {\n    transform: rotate(270deg);\n\n    .circle-9 {\n      animation-delay: -0.3s;\n    }\n  }\n\n  .block-10 {\n    transform: rotate(300deg);\n\n    .circle-10 {\n      animation-delay: -0.2s;\n    }\n  }\n\n  .block-11 {\n    transform: rotate(330deg);\n\n    .circle-11 {\n      animation-delay: -0.1s;\n    }\n  }\n}\n\n@keyframes dpzvc-spinner-fading-circle {\n\n  0%,\n  39%,\n  100% {\n    opacity: 0;\n  }\n\n  40% {\n    opacity: 1;\n  }\n}\n\n// ---------- Snake Spinner ----------\n.dpzvc-spinner-snake {\n  animation: dpzvc-spinner-snake 0.8s infinite linear;\n  border-style: solid;\n  border-color: transparent;\n  border-radius: 50%;\n  box-sizing: border-box;\n  height: @SpinnerSize;\n  width: @SpinnerSize;\n  border-width: @SpinnerSize / 8;\n  border-top-color: @SpinnerColor;\n  border-left-color: @SpinnerColor;\n  border-bottom-color: @SpinnerColor;\n}\n\n@keyframes dpzvc-spinner-snake {\n  0% {\n    transform: rotate(0deg);\n  }\n\n  100% {\n    transform: rotate(360deg);\n  }\n}",".dpzvc-loadmore {\n  height: 100%;\n  overflow-y: scroll;\n  box-sizing: border-box;\n\n  //-webkit-overflow-scrolling: touch;\n  &-top,\n  &-bottom {\n\n    position: relative;\n    height: 0;\n    line-height: 40px;\n    overflow: hidden;\n    font-size: 14px;\n    transform: translateZ(0);\n\n    .dpzvc-loadmore-status {\n      vertical-align: middle;\n      height: 40px;\n      line-height: 40px;\n      text-align: center;\n\n      .spinner {\n        display: inline-block;\n        vertical-align: middle;\n      }\n\n      .dpzvc-loadmore-text {\n        margin-left: 5px;\n        vertical-align: middle;\n      }\n    }\n  }\n\n  &-bottom {\n    height: 40px;\n  }\n\n  &-content {\n    height: auto;\n    background: #ffffff;\n\n  }\n\n  &-drag {\n    transition: all .3s linear;\n  }\n}",".dpzvc-popup {\n\n  &-mask {\n    position: absolute;\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    background: rgba(0, 0, 0, .7);\n  }\n\n  &-content {\n    background: #ffffff;\n    position: absolute;\n    z-index: 300;\n  }\n\n  &-left {\n    top: 0;\n    bottom: 0;\n    left: 0;\n  }\n\n  &-right {\n    top: 0;\n    bottom: 0;\n    right: 0;\n  }\n\n  &-top {\n    top: 0;\n    left: 0;\n    right: 0;\n    text-align: center;\n    min-height: 50px;\n    line-height: 50px;\n    color: #ffffff;\n    background: rgba(0, 0, 0, .7);\n  }\n\n  &-bottom {\n    left: 0;\n    right: 0;\n    bottom: 0;\n  }\n}",".dpzvc-Indicator {\n  width: 100%;\n  position: absolute;\n  height: 100%;\n  left: 0;\n  top: 0;\n\n  &-container {\n    position: relative;\